import { configureStore,createSlice } from "@reduxjs/toolkit";





let user = createSlice({               //createSlice()  
    name:'user',
    initialState: {name:'kim',age:20},                     //redux state -> state  ,      store.js . 
    reducers : { //'kim john kim '
        changeName(state){
            return state.age = state.age +1;
        },
        increase(state,a){
            state.age +=a.payload; //a   .payload   . payload = , .
        }
    }
})
export let {changeName,increase} = user.actions;


let stock = createSlice({               //createSlice()  
    name:'stock',
    initialState: [10,11,12]
})


let cartProduct = createSlice({               //createSlice()  
    name:'cartProduct',
    initialState: [],
    reducers:{
        qtyIncrease(state, id){
           state[id.payload].count += 1;
        },
        qtyMinus(state, id){
            if( state[id.payload].count === 0){
                state[id.payload].count = 0;
            }else{
                state[id.payload].count =  state[id.payload].count - 1;
            }
        },
        qtyCal(state,id){
            state[id.payload].total= state[id.payload].price * state[id.payload].count;

        },
        addItem(state, item){
                state.push(item.payload);
                alert("Added " + '"'+item.payload.title +'"'+ " On your Cart")

        },
        removeItem(state, id){
           let copy = [...state]
           let copy2 = copy.filter((e)=>{return e.id !== id.payload;})//Filter    .
           return copy2
        },
        setQty(state, qty,id){

        }
    }
})
export let{qtyIncrease, qtyMinus, addItem,qtyCal, removeItem,setQty} = cartProduct.actions


export default configureStore({ //   
    reducer: {
        user : user.reducer,   //: user.reducer .reducer 
        stock : stock.reducer,
        cartProduct : cartProduct.reducer 
    }
})