import React from 'react';
import '../App.css';
import {useParams} from 'react-router-dom';
import {data} from '../data'
import {useState, useEffect} from 'react';
import axios from 'axios'
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import {addItem} from './../store.js'
import {useDispatch, useSelector} from 'react-redux';
import {setQty} from "./../store";











function Detail(props){
    let {detailFade,setDetailFade} = useState('');
    let { id } = useParams();
    let [itemName,setItemName] = useState(data);
    let dispatch = useDispatch();
    let cartList = useSelector((state)=>{return state.cartProduct})
    let [itemqty, setItemqty] = useState(1);
    
    let [alertTime,setAlert] = useState(true);
    let [timeUp,setTimeUp] = useState(5);

    let [anotherRecommendation, setRecommendation] = useState();
    let [show, setShow] = useState(false);
    let [tabChange, setTabChange] = useState(0);
    let[fade, setFade] = useState('');


   
    useEffect(()=>{
        let localData = localStorage.getItem('watched')
        localData = JSON.parse(localData)
        localData.push(id)
        localData = new Set(localData) //set  
        localData = Array.from(localData)// set array . 
        localStorage.setItem('watched', JSON.stringify(localData))
      }, [])
    

    const startEnd = useEffect((tabChnage)=>{
        setTimeout(()=>{setFade('end')},100)
        return () =>{
            setFade('')
        }
    },[tabChange])

    useEffect(()=>{
        let interval = setTimeout(()=>{setAlert(false);
            console.log(setTimeUp(timeUp-1));
        },5000);
        return clearInterval(interval);
    })

    const freeCode = ()=>{
        alert('Promo Code : ABCDEF1234');
    }


    let {num,setNum} =useState();
    const checkNum =(e) =>{
        setNum({...num})
    }    

  

    return(
        <div className={`detailstart${detailFade}`}>
                {
                    alertTime === true ?    
                    <div className="promoBox">
                        <button onClick={freeCode}> Get a Promo Code within {timeUp} Sec</button>
                    </div> : null
                }
                <div className="detail-img-box">
                    <div className="imgBox">
                    <img className="detailImages" src={require('../img/dogfood'+id+'.png')}></img>
                    </div>


                    <div className="detail-desc">
                        <h2 className="nameHead"> {itemName[id].title}</h2>

                            <div className="itemInfoBox">
                                <p>Reviews : </p>   
                                <p className="madeBy">{itemName[id].content}</p>
                                <p>Price: {itemName[id].price}$</p>
                                <p className="itemId">Item Id: {id}</p>
                            </div>
                              
                                

                                <form id='myform' method='POST' className='quantity' action='#' >
                                
                                    <p className="qty-title">Quantity : </p>  
                                    
                                    <input onClick={(e)=>{
                                        if(itemqty >0){setItemqty(itemqty -1)}
                                        }} 
                                        type='button' value='-' className='qtyminus minus' field='quantity'  />
                                    
                                    <input type='text' name='quantity' value={itemqty} className='qty' />

                                    <input onClick={(e)=>{
                                        setItemqty(itemqty +1)
                                        }}
                                        type='button' value='+' className='qtyplus plus' field='quantity' />
                                    
                                    
                                </form>
                                <Button className="addToCartBtn" type="button" onClick={()=>{
                                    let copy = [{...itemName[id]}]
                                    let copy2 = copy[0].count = itemqty
                                    dispatch(addItem(...copy))
                                    }    
                                    }>Add to cart</Button>
                                       
                    </div>
                </div>

                <div className="recommendationBox">
                        <div className="recommendationList">
                            <button onClick={()=>{
                                axios.get('https://codingapple1.github.io/shop/data2.json')
                                .then((result)=>{ setRecommendation(result.data) 
                                setShow(!show)})
                                .catch(()=>{console.log('.')})
                            }}> From API another Recommendation </button>
                            <div className="otherSiteItems">
                            {show && anotherRecommendation.map(function(a,i){
                                return(
                                    <div className="SiteItems" key={i}>
                                        <h6>Item Name: {anotherRecommendation[i].title}</h6>
                                    </div>
                                )
                                })
                            }
                            </div>
                        </div>
                </div>
                <div className="navDetail">
                <Nav variant="tabs" defaultActiveKey="link0">
                    <Nav.Item>
                        <Nav.Link onClick={()=>{setTabChange(0)}} eventKey="link0">Product Description</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={()=>{setTabChange(1)}} eventKey="link-1">Specification</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={()=>{setTabChange(2)}}eventKey="link-2">Reviews</Nav.Link>
                    </Nav.Item>
                </Nav>
                </div>
                <div className="tabGroup">
                        <div className={`start ${fade}`}>
                            <TabShow  tabProps={tabChange}></TabShow>
                        </div>
                </div>
                

                
            
        </div>
    )
}

function TabShow(props){
    if (props.tabProps === 0){
        return (
            <div>
        <h3>Product Description</h3>
        <div>Dogs are ancestral carnivores, so their Biologically Appropriate diet is rich and varied in whole animal ingredients. ORIJEN Original Dog Food is packed with 85% quality animal ingredients fresh or raw like free-run chicken and turkey, and wild-caught fish. With rich inclusions of WholePrey animals, this recipe is made with the most nutrient-dense, succulent parts of the prey to deliver the nutrition dogs need. The result is a diet for your canine companion that is beyond comparison.</div><br/><br/>
        </div>
        )
    }else if (props.tabProps ===1){
        return (
        <div>
        <h2>Sepecification</h2>
        <h4>Ingredients</h4>
        <div>Raw Beef (12%), Raw Wild Boar (5%), Raw Bison (5%), Raw Lamb (5%), Raw Pork (5%), Raw Beef Liver (4%), Raw Whole Pilchard (4%), Fresh Eggs (4%), Raw Pork Liver (4%), Dehydrated Lamb (4%), Dehydrated Beef (4%), Dehydrated Herring (4%), Dehydrated Mutton (4%), Dehydrated Pork (4%), Dehydrated Sardine (4%), Whole Red Lentils, Whole Peas, Whole Chickpeas, Whole Green Lentils, Whole Pinto Beans, Lentil Fibre, Pea Starch, Pollock Oil (2%), Raw Lamb Liver (2%), Beef Fat (2%), Raw Wild Boar Liver (2%), Raw Beef Kidney (2%), Raw Beef Tripe (1%), Raw Lamb Kidney (1%), Raw Lamb Tripe (1%), Dried Kelp, Fresh Whole Pumpkin, Fresh Whole Butternut Squash, Fresh Whole Zucchini, Fresh Whole Carrots, Fresh Whole Apples, Fresh Whole Pears, Dried Chicory Root, Fresh Kale, Fresh Spinach, Fresh Beet Greens, Fresh Turnip Greens, Whole Cranberries, Whole Blueberries, Whole Saskatoon Berries, Turmeric, Milk Thistle, Burdock Root, Lavender, Marshmallow Root, Rosehips.

        ADDITIVES (per kg) Technological additives: Tocopherol extract from vegetable oils: 121mg, Citric Acid: 40mg. Sensory additives: Rosemary extract: 80mg. Nutritional additives: 3b606 (Zinc: 105mg), 3a821 Vitamin B1: 10 mg, 3a700 Vitamin E: 100 IU. Zootechnical additives: 4b1707 Enterococcus faecium NCIMB 10415 1x10^9 CFU.</div>
        <br/><br/>
        </div>
        )
    }else if (props.tabProps ===2){
        return (<div><h3>Reviews</h3></div>)
    }
}


export default Detail;