import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {useState} from 'react';


function Login() {
  const [inputData, setInputData] = useState({
    email:"",
    pwd:""
  });


  const handleInput =(e)=>{
    const {name,value} = e.target;
    console.log(e.target.value);
    console.log(e.target.name);
    setInputData({
      ...inputData,
      [name] : value,
    })
  }

  //Login Validation

  const emailValid = inputData.email.includes('@') && inputData.email.includes('.');
  const specialLetter = inputData.pwd.search(/[`~!@@#$%^&*|'";:/?]/gi);
  const pwdValid = inputData.pwd.length >= 6 && specialLetter === 0 ? true : false;

  const [loginVerified,setLoginVerified] = useState('false');

  const handleSubmit = ()=>{
    if(inputData.email === "1@test.com" &&
    inputData.pwd === "!123456"    
  )
  {alert("You logged in with Test ID")
  }
    else if(!emailValid ||
      !pwdValid
    ){ console.log(emailValid, specialLetter, pwdValid)
      alert("Email or Password is NOT invalid. ")
    }else {
      alert("Success")
    }
  }

  return (
    <Form className='loginForm'>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label><h6>Email Address</h6></Form.Label>
        <Form.Control onChange={handleInput} name="email" className="emailBox" type="email" placeholder="email" />
        <Form.Text className="text-muted">
          <br></br>
          Test ID: 1@test.com , Password:!123456
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label><h6>Password</h6></Form.Label>
        <Form.Control onChange= {handleInput} name="pwd" className="pwdBox" type="password" placeholder="Password" />
      </Form.Group>
      
      <Button onClick={handleSubmit} variant="primary" type="submit">
        Login
      </Button>
    </Form>
  );
}

export default Login;