import React from 'react';
import { useState } from 'react';
import {data} from'../data.js'
import {Link, useNavigate} from 'react-router-dom';
import '../App.css';


function GearItemLists(){
    let [itemName,setItemName] = useState(
        (()=>{return data.filter((item)=>{return item.category === "Gear"})})
        );

  
        const lowPriceFilter = (catItem)=>{
            const result = itemName.sort((a,b) => (a.price - b.price));
        }
    
        const highPriceFilter = (catItem)=>{
            const result = itemName.sort((a,b) => (b.price - a.price));
        }
    
        const allFilter = (catItem)=>{
            const result =itemName.sort((a,b) => (a.id - b.id));
        }
   


    return(
        <div>
            <section id="content_box">
            <div className="box">
              <h3>Gear Products</h3>
              <div className="filterBox">
                <Link><p onClick={allFilter}>All</p></Link>
                <Link><p onClick={highPriceFilter}> High Price </p></Link>
                <Link><p onClick={lowPriceFilter}>Low Price</p></Link>
              </div>
                <div className="clear"></div>
            {
                itemName.map(function(a, i){
                    return(
                    <ul className="items" key={i}>
                        <Link to={`/detail/${itemName[i].id}`}><img className="images"  src={require('../img/dogfood'+itemName[i].id+'.png')} ></img></Link>
                        <Link to={`/detail/${itemName[i].id}`} style={{textDecoration:'none'}}><li className="a">{itemName[i].title}</li></Link>
                        <li className="b">Price: {itemName[i].price}$</li>
                        <li className="c">{itemName[i].content}</li>
                     </ul>
                    )
                    
                })
            }
                </div>
            <div className="clear"></div>
            </section>
        </div>
    )
}


export default GearItemLists;