import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import {useState,useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {changeName,increase,qtyIncrease,qtyMinus,qtyCal, removeItem} from "./../store";


function Cart(){


    let cartList = useSelector((state)=>{return state.cartProduct}) // redux store     import {useSelector}.// store   state  
    
    let state = useSelector((state)=> {return state})
    let dispatch = useDispatch();//message .

    let [totalPrice, setTotalPrice] = useState('');

    let subTotal = 0;
    let subTotalCal = cartList.map(function(a,i){
        subTotal = subTotal + cartList[i].total;
        return subTotal
    })
    console.log(cartList)
   

    return(
    <div>
        <h3 className="cartHeader">Your Cart</h3>
        <Table  className="cartTables" striped >
        <thead>
            <tr>
            <th></th>
            <th>Product Name</th>
            <th></th>
            <th >Quantity</th>
            <th></th>
            <th>Price</th>
            </tr>
        </thead>
        {
            cartList.map(function(a, i){
                dispatch(qtyCal(i));
            return(
                <tbody className="cartList" key={i}>
                    <tr>
                    <td>     </td>
                    <td>{cartList[i].title}</td>
                    <td><Button className='cartQtyMinus' variant="secondary" 
                        onClick={()=>{dispatch(qtyMinus(i));}}>-</Button></td>
                    <td>{cartList[i].count}</td>
                    <td><Button className='cartQtyPlus' variant="secondary" 
                        onClick={()=>{dispatch(qtyIncrease(i));}} >+</Button></td>
                    <td>${cartList[i].price * cartList[i].count}</td>

                    <td><Button variant="outline-danger"
                        onClick={()=>{dispatch(removeItem(cartList[i].id))}}>Delete</Button></td>
                    </tr>
                </tbody>

            )
            })
 
           
        }

        </Table>

        <div className="cartTotal">
        <Table >
            <tbody>
                <tr>
                <td><h5>Total :</h5></td>
                <td><h5>{(subTotal + (subTotal * 0.13)).toFixed(2)}</h5></td>
                <td>GST: {(subTotal * 0.13).toFixed(2)}</td>
                <td>SubTotal :{subTotal}$</td>
                </tr>
            </tbody>
        </Table>

        </div>
    </div>
    )
}




export default Cart;